@import url("https://fonts.googleapis.com/css2?family=Karma:wght@500&display=swap");
body {
    width: 80%;
    margin: 0 auto;
}

.app-wrapper {
    margin: 0 auto;
    display: grid;
    grid-template-areas: "h h" "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    row-gap: 20px;
    column-gap: 15px;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #00539C;
    color: #fff;
}